.yellow_flower {
  top: 30vh;
  left: -1vw;
  transform:  translateY(-50%); }

.yellow_pat {
  top: 2vh;
  right: 30vw;
  transform: translateX(50%) rotate(-12deg); }

.yellow_bottle {
  bottom: 10vh;
  right: 16vw;
  transform: translateX(50%) rotate(15deg);
  width: 20vw; }

.yellow_box {
  bottom: -10vh;
  left: 10vw;
  transform:  rotate(-10deg); }




.progress {
  @include position(absolute, null null 0 null );
  z-index: 10;
  // width: 100%
  height: 15px;
  background-color: #fff; }
